<template>
  <Navbar :currentPage="currentPage"></Navbar>
    <section class="section section-home" id="section-home">
        <div class="container">
            <div class="home-slider">
                <div class="slide"
                  :class="{ active: currentIndex === index }"
                  v-for="(item, index) in indexSlide" :key="item">
                    <div class="content">
                        <span>{{ item.subtitle }}</span>
                        <h3>{{ item.title }}</h3>
                        <p>{{ item.content }} </p>
                        <a href="#" class="btn"></a>
                    </div>
                    <div class="image">
                        <img :src="item.imageSrc">
                    </div>
                </div>
            </div>

            <div id="home-slide-next" class="fas fa-angle-right" @click="nextPage"
            ></div>
            <div id="home-slide-prev" class="fas fa-angle-left" @click="prePage"
              ></div>
        </div>
    </section>
    <section class="section section-topic" id="section-topic">
        <div class="container">
            <div class="item-group">
                <a class="item">
                    <span class="new">New</span>
                    <i class="fa-solid fa-piggy-bank"></i>
                    <h3 class="title"> </h3>
                    <p>0</p>
                    <div class="block-btn-more">
                      <div class="btn"></div>
                    </div>
                </a>
                <a class="item">
                    <span class="new">New</span>
                    <i class="fa-solid fa-comments-dollar"></i>
                    <h3 class="title"></h3>
                    <p></p>
                    <div class="block-btn-more">
                        <div class="btn"></div>
                    </div>
                </a>
                <a class="item">
                    <span class="new">New</span>
                    <i class="fa-solid fa-comment-dots"></i>
                    <h3 class="title"></h3>
                    <p></p>
                    <div class="block-btn-more">
                        <div class="btn"></div>
                    </div>
                </a>
            </div>
        </div>
    </section>
    <section class="section section-service" id="section-service">
        <div class="container">
            <Swiper class="service-slider swiper"
              :pagination="{ clickable: true }"
              :breakpoints="swiperOptions.breakpoints"
              :autoplay="{  autoplay: true,
                            disableOnInteraction: false,
                            stopOnLastSlide: false}"
              loop
              :centeredSlides="true"
              >
                    <SwiperSlide class="slide swiper-slide"
                      >
                        <img src="@/assets/images/001.jpg" alt="">
                        <div class="content">
                            <h3 class="title"></h3>
                            <p></p>
                            <div class="block-btn">
                                <a href="javascript:;" class="btn"></a>
                            </div>
                        </div>
                    </SwiperSlide>
                    <SwiperSlide class="slide swiper-slide">
                        <img src="@/assets/images/002.jpg" alt="">
                        <div class="content">                            <h3 class="title"></h3>
                            <p></p>
                            <div class="block-btn">
                                <a href="javascript:;" class="btn"></a>
                            </div>
                        </div>
                    </SwiperSlide>
                    <SwiperSlide class="slide swiper-slide">
                        <img src="@/assets/images/003.jpg" alt="">
                        <div class="content">
                            <h3 class="title">REITS</h3>
                            <p>
                              (REITs)</p>
                            <div class="block-btn">
                                <a href="javascript:;" class="btn"></a>
                            </div>
                        </div>
                    </SwiperSlide>
                    <SwiperSlide class="slide swiper-slide">
                        <img src="@/assets/images/004.jpg" alt="">
                        <div class="content">
                            <h3 class="title"></h3>
                            <p></p>
                            <div class="block-btn">
                                <a href="javascript:;" class="btn"></a>
                            </div>
                        </div>
                    </SwiperSlide>

                <div class="swiper-pagination"></div>
            </Swiper>
        </div>
    </section>
</template>
<script>
import Navbar from '@/components/Navbar.vue'
import goTop from '@/methods/goTop'
// swiper core
import SwiperCore, { Navigation, Pagination, Autoplay } from 'swiper'
// swiper vue
import { Swiper, SwiperSlide } from 'swiper/vue/swiper-vue'
// swiper css
import 'swiper/swiper-bundle.css'

SwiperCore.use([Navigation, Pagination, Autoplay])

export default {
  data () {
    return {
      currentPage: this.$route.fullPath,
      swiperOptions: {
        breakpoints: {
          0: { slidesPerView: 1, spaceBetween: 30 },
          768: { slidesPerView: 2, spaceBetween: 30 },
          1200: { slidesPerView: 3, spaceBetween: 30 }
        }
      },
      showNextBtn: false,
      sidePage: 0,
      indexSlide: [
        {
          title: '',
          subtitle: '',
          imageSrc: 'https://i.postimg.cc/cH0LytJk/phone.png',
          content: '6'
        },
        {
          title: '',
          subtitle: '',
          imageSrc: 'https://i.postimg.cc/rpgvsDJP/final0.png',
          content: ''
        }
      ],
      currentIndex: 0,
      slideLen: ''
    }
  },
  components: {
    Navbar,
    Swiper,
    SwiperSlide
  },
  methods: {
    nextPage () {
      this.slideLen = this.indexSlide.length
      this.currentIndex = (this.currentIndex + 1) % this.slideLen
    },
    prePage () {
      this.currentIndex = ((this.currentIndex - 1) + this.slideLen) % this.slideLen
    }
  },
  mounted () {
    goTop()
  }
}
</script>
